
<style scoped>

</style>


<template>
    <div>
        <el-row>
            <el-col :span="8">

            </el-col>
            <el-col :span="4">

            </el-col>
        </el-row>
        <el-card>
            <el-table>
                <el-table-column 
                    prop='goods_id' label='#'
                />
                <el-table-column 
                    prop='goods_name' label='商品名称'
                />
                <el-table-column 
                    prop='goods_price' label='商品价格(元)'
                />
                <el-table-column
                    prop='goods_weight' label='商品重量'
                />
                <el-table-column
                    prop='add_time' label='创建时间'
                >
                    <template slot-scope="key_obj">
                        <div v-if="0">{{ key_obj.row }}</div>
                        
                    </template>
                </el-table-column>
                <el-table-column
                    label='操作'
                >

                </el-table-column>
            </el-table>
        </el-card>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="current_page"
                :page-sizes="page_sizes"
                :page-size="page_size"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
    </div>
</template>


<script>
export default {
    name:'productlist',
    data() {
        return {
            goodlist:null,
            current_page:1,
            page_sizes:[2,5,10,15],
            page_size:2,
            total:3,
        };
    },
    created(){

    },
    mounted(){
        this.oninit()
    },
    //props:{},//[]
    methods:{
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.page_size=val
            this.oninit()
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.current_page=val
            this.oninit()
        },
        async oninit(){
            let {data:res}=await this.$http.get(
                `/goods`,{params:{}}
            )
            console.log('获取列表信息',res)
            this.goodlist=res.data
        }   
    },
    computed:{

    },
    watch:{

    },
    filters:{

    },
    directives:{

    },
    components:{

    },
};
</script>